import React from 'react';
import NovelReader from '../NovelReader';
import { meta_processors } from '../../metaProcessor';
import fetcher from '../../fetcherimpl';

const View = () => {
  return (
    <>
      {Object
        .keys(meta_processors.view_component)
        .filter((it) => meta_processors.view_component.hasOwnProperty(it))
        .map((it) => meta_processors.view_component[it])
        .map((It) => <It />)}
      <NovelReader fetcher={fetcher} />
    </>
  );
};

export default View;
