/**
 * モバイルか否かを判定
 * @returns モバイルなら`true`、そうでないなら`false`
 */
export function isMobile(): boolean {
  return getComputedStyle(document.body).getPropertyValue('--is-mobile').trim() === 'yes';
}

/**
 * 画面が横向きか否かを判定
 * @returns 横向きなら`true`、そうでないなら`false`
 */
export function isLandscape(): boolean {
  return getComputedStyle(document.body).getPropertyValue('--is-landscape').trim() === 'yes';
}

let chrome: boolean;

export function isChrome(): boolean {
  if (typeof chrome === 'undefined') {
    const agent = window.navigator.userAgent.toLowerCase();
    chrome = (agent.indexOf('chrome') !== -1) && (agent.indexOf('edge') === -1) && (agent.indexOf('opr') === -1);
  }
  return chrome;
}

export interface DoFunction {
  (): void;
}

export function doAfter(func: DoFunction) {
  setTimeout(func, 10);
}
