/* eslint-disable max-classes-per-file, no-unused-vars */
import React, { Component, FC, createRef } from 'react';
import { useInView } from 'react-intersection-observer';
import {
  isLandscape, isMobile, isChrome, doAfter,
} from '../utils';
import { meta_processors } from '../metaProcessor';
import NextMenu from './NextMenu';
import NovelFetchError from '../fetcherimpl/NovelFetchError';

interface ReaderProps {
  fetcher: NovelFetcher;
}

interface ReaderState {
  episode?: Episode;
  novel_meta?: NovelMeta;
  title_hidden?: boolean;
  menu_hidden?: boolean;
  next_hidden?: boolean;
  settings_shown?: boolean;
  error?: FetchErrorType;
}

type PosFlag = 'last' | 'start';

type NovelLineProps = {
  item: EpisodeLine,
  scroll_dir: () => 'indeterminate' | 'forward' | 'backward',
  set_title_visibility: (visibility: boolean) => void
  set_next_visibility: (visibility: boolean) => void
  position?: PosFlag;
  cp_key: number;
};

const TextSpanRenderer: FC<{ item: TextSpanElement }> = (props: { item: TextSpanElement }) => {
  const { item } = props;
  if (typeof item === 'string') { return <>{item}</>; }

  const child_props: ChildProps<HTMLSpanElement> = {};

  (item.meta || []).forEach((it) => {
    if (meta_processors.text_span.hasOwnProperty(it.id)) {
      meta_processors.text_span[it.id](it.value, child_props);
    } else {
      console.warn(`text-span meta data processor for id '${it.id}' is not defined.`);
    }
  });

  return (
    <span className={(item.meta || []).filter((it) => it.id === 'class').map((it) => it.value).join(' ')} {...child_props}>
      {item.text}
    </span>
  );
};

const getRect = () => (
  document.querySelector('.novel-body')
  || {
    getClientRects: () => ([
      {
        right: 0,
        left: 0,
        top: 0,
        bottom: 0,
      },
    ]),
  }).getClientRects()[0];

const previousStates: ({
  props: ChildProps<HTMLParagraphElement>,
  state: boolean} | undefined)[] = [];

const getEpisodeId = (): { nid?: string, eod?: string } => (
  (window.location.hash.match(/^#\/view\/(?<nid>[a-zA-Z0-9]{8})\/(?<eod>[0-9]+)$/) || {}).groups || {}
);

const NovelLineElement: FC<NovelLineProps> = (props: NovelLineProps) => {
  const {
    item,
    scroll_dir,
    position: pos,
    cp_key: key,
  } = props;
  const rect = getRect();

  const [ref, in_view] = useInView({
    threshold: 0.1,
    rootMargin: `${rect.top}px ${rect.right}px ${rect.bottom}px ${rect.left}px`,
  });

  let state: {props: ChildProps<HTMLParagraphElement>, state: boolean};

  switch (pos) {
  case 'start':
    if (!previousStates[0]) {
      state = { props: {}, state: !in_view };
    } else {
      state = previousStates[0]!;
    }

    if (state.state !== in_view) {
      if (in_view) {
        props.set_title_visibility(true);
      } else {
        props.set_title_visibility(false);
      }
      state.state = in_view;
      previousStates[0] = state;
    }
    break;

  case 'last':
    if (!previousStates[key]) {
      state = { props: {}, state: !in_view };
    } else {
      state = previousStates[key]!;
    }

    if (state.state !== in_view) {
      if (in_view) {
        props.set_next_visibility(true);
      } else {
        props.set_next_visibility(false);
      }
      state.state = in_view;
      previousStates[key] = state;
    }
    break;
  default:
    break;
  }

  if (typeof item === 'string') {
    return <p ref={ref} className={`novel-line${in_view ? ' shown' : ''}`}>{item}</p>;
  }

  let child_props: ChildProps<HTMLParagraphElement>;
  const ps = previousStates[key];

  if (ps && ps.state === in_view) {
    child_props = ps.props;
  } else {
    child_props = {};
    (item.meta || []).forEach((it) => {
      if (meta_processors.text_line.hasOwnProperty(it.id)) {
        meta_processors.text_line[it.id](
          it.value,
          child_props,
          {
            displaying: in_view,
            scroll_dir: scroll_dir(),
          },
        );
      } else {
        console.warn(`text-line meta data processor for id '${it.id}' is not defined.`);
      }
    });
    previousStates[key] = {
      state: in_view,
      props: child_props,
    };
  }

  return (
    <p ref={ref} className={`novel-line${in_view ? ' shown' : ''}`} {...child_props}>
      {item.texts.map((it, idx) => <TextSpanRenderer item={it} key={idx} />)}
    </p>
  );
};

function setStyleVars(prop: string, val: string) {
  document.documentElement.style.setProperty(`--${prop}`, val);
}

export default class NovelReader extends Component<ReaderProps, ReaderState> {
  novel_body_ref: React.RefObject<HTMLDivElement>;

  previous_scroll_top: number;

  current_scroll_direction: 'indeterminate' | 'forward' | 'backward' = 'indeterminate';

  previous_image?: string;

  constructor(props: ReaderProps) {
    super(props);
    this.state = {};
    this.novel_body_ref = createRef();
    this.previous_scroll_top = 0;
  }

  componentDidMount() {
    const { nid, eod } = getEpisodeId();
    if (!nid || !eod) {
      this.setState({
        error: {
          type: 'unknown',
          phase: 'fetch',
          detail: `urlが不正です。`,
        },
      });
    } else {
      this.props.fetcher
        .fetchEpisode(nid, eod)
        .then((novel) => {
          meta_processors.finalizer.forEach((it) => it());
          if (typeof novel.episode === 'undefined') {
            this.setState({
              error: {
                type: 'unknown',
                phase: 'unknown',
                detail: `fetch succeeded, but server returned null on id '${nid}', episode ${eod}`,
              },
            });
            return;
          }
          const nov = novel.episode;
          meta_processors.initializer.forEach((it) => it(nov));
          this.setState({ episode: nov, novel_meta: novel.meta });
        })
        .catch((err: NovelFetchError) => {
          this.setState({ error: err.source });
        });
    }

    window.addEventListener('orientationchange', this.orientationChange);

    const hd = document.querySelector('.heading');
    const ft = document.querySelector('.footing');
    hd!.classList.add('hidden');
    ft!.classList.add('hidden');
  }

  componentDidUpdate() {
    if (!this.novel_body_ref.current) { return; }

    const elem = document.querySelector('.scroll-target')! as HTMLDivElement;
    elem.style.height = `${this.novel_body_ref.current.scrollWidth * 5}px`;
  }

  componentWillUnmount() {
    meta_processors.finalizer.forEach((it) => it());
    window.removeEventListener('orientationchange', this.orientationChange);

    const hd = document.querySelector('.heading');
    const ft = document.querySelector('.footing');
    hd!.classList.remove('hidden');
    ft!.classList.remove('hidden');
  }

  settingsView() {
    return (
      <div className="modal settings" onClick={() => this.setState({ settings_shown: false })}>
        <div className="container" onClick={(evt) => evt.stopPropagation()}>
          <button
            className="close"
            onClick={() => this.setState({ settings_shown: false })}
            type="button"
          >
            <i className="material-icons">close</i>
          </button>
          <div className="settings-fields">
            <div className="fonts">
              <div className="size">
                <button
                  type="button"
                  onClick={() => setStyleVars('font-size-factor', '1.5')}
                  style={{ fontSize: '1.8rem' }}
                >
                  極大
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-size-factor', '1.2')}
                  style={{ fontSize: '1.44rem' }}
                >
                  大
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-size-factor', '1')}
                  style={{ fontSize: '1.2rem' }}
                >
                  中
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-size-factor', '0.8')}
                  style={{ fontSize: '0.96rem' }}
                >
                  小
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-size-factor', '0.5')}
                  style={{ fontSize: '0.6rem' }}
                >
                  極小
                </button>
              </div>
              <div className="font-variants">
                <button
                  type="button"
                  onClick={() => setStyleVars('font-variant', 'sans-serif')}
                  style={{ fontFamily: 'sans-serif' }}
                >
                  Aa あ
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-variant', 'serif')}
                  style={{ fontFamily: 'serif' }}
                >
                  Aa あ
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-variant', 'cursive')}
                  style={{ fontFamily: 'cursive' }}
                >
                  Aa あ
                </button>
                <button
                  type="button"
                  onClick={() => setStyleVars('font-variant', 'fantasy')}
                  style={{ fontFamily: 'fantasy' }}
                >
                  Aa あ
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  orientationChange = () => {
    setTimeout(() => this.forceUpdate(), 100);
  }

  scrollHandler = (evt: React.UIEvent<HTMLElement>) => {
    if (!this.state.episode || !this.novel_body_ref.current) { return; }

    const current_scroll_top = (evt.target as HTMLElement).scrollTop / 5;
    let scroll_delta;

    if (isChrome()) {
      const rect = this.novel_body_ref.current.getClientRects()[0];
      scroll_delta = Math.floor(
        this.novel_body_ref.current.scrollWidth - current_scroll_top - rect.width,
      );
    } else {
      scroll_delta = -current_scroll_top;
    }

    this.novel_body_ref.current.scrollLeft = scroll_delta;
    this.current_scroll_direction = (
      this.previous_scroll_top < current_scroll_top
        ? 'forward'
        : 'backward');
    this.previous_scroll_top = current_scroll_top;
  }

  render() {
    if (this.state.error) {
      switch (this.state.error) {
      case 'novel_not_found':
        return (
          <div className="reader">
            <div className="modal">
              <span className="modal-content_single">
                小説が見つかりません。削除済みか、URLが間違っています。
              </span>
            </div>
          </div>
        );
      case 'episode_not_found':
        return (
          <div className="reader">
            <div className="modal">
              <span className="modal-content_single">
                指定された小説の部分が見つかりません。削除済みか未投稿、またはURLが間違っています。
              </span>
            </div>
          </div>
        );
      default:
        return (
          <div className="reader">
            <div className="modal">
              <span className="modal-content_single">
                技術的な問題が発生しました。お手数ですが、エラーメッセージを添えてお問い合わせください。
                <span className="error-details">{this.state.error.detail}</span>
              </span>
            </div>
          </div>
        );
      }
    }
    if (!this.state.episode) {
      return (<div className="reader"><div className="spinner" /></div>);
    }

    const ept = (this.state.episode.meta || {}).sub_title || 'タイトル未設定';
    // const nvt = (this.state.novel_meta || {}).title || 'タイトル未設定';

    return (
      <div className="reader">
        <div className="scroll-area" onScroll={this.scrollHandler}>
          <div className="scroll-target" />
        </div>
        {Object
          .keys(meta_processors.child_component)
          .filter((it) => meta_processors.child_component.hasOwnProperty(it))
          .map((it) => meta_processors.child_component[it])
          .map((It) => <It />)}
        <div className={`title ${ this.state.title_hidden ? 'hidden' : ''}`}>
          <h1>{ept}</h1>
        </div>
        <NextMenu
          visible={this.state.next_hidden}
          ep_meta={this.state.episode.meta}
          nv_meta={this.state.novel_meta}
        />
        <div className="novel-body" ref={this.novel_body_ref}>
          {this.state.episode.lines.map(
            (it, idx) => (
              <NovelLineElement
                item={it}
                key={idx}
                cp_key={idx}
                set_title_visibility={
                  (vis) => doAfter(() => this.setState({ title_hidden: !vis }))
                }
                set_next_visibility={
                  (vis) => doAfter(() => this.setState({ next_hidden: vis }))
                }
                // eslint-disable-next-line no-nested-ternary
                position={idx === 0 ? 'start' : idx === (this.state.episode!.lines.length - 1) ? 'last' : undefined}
                scroll_dir={() => this.current_scroll_direction}
              />
            ),
          )}
        </div>
        <button className="settings-open" type="button" onClick={() => this.setState({ settings_shown: true })}>
          <i className="material-icons">settings</i>
        </button>
        {this.state.settings_shown ? this.settingsView() : null}
        {(isMobile() && !isLandscape())
          ? (
            <div className="modal">
              <span className="modal-content_single">画面を横向きにしてお楽しみください</span>
            </div>
          )
          : null}
      </div>
    );
  }
}
