import React, { FC } from 'react';
import { Link } from 'react-router-dom';

interface Props {
  visible?: boolean;
  ep_meta?: EpisodeMeta;
  nv_meta?: NovelMeta;
}

const getEpOrder = () => {
  const co = window.location.hash.match(/\/(?<num>\d+)$/);
  if (!co) {
    return NaN;
  }
  return parseInt((co.groups || { num: 'NaN' }).num, 10);
};

const NextMenuBody: FC<{ep_meta: EpisodeMeta, nv_meta?: NovelMeta}> = (props) => {
  const order = getEpOrder();
  console.log(props.ep_meta);
  return (
    <>
      <div className="next-menu-title">
        {(props.nv_meta || {}).title || 'タイトル未設定'}
      </div>
      <div className="episodes">
        <div className={`next${props.ep_meta.has_next && !Number.isNaN(order) ? '' : ' disabled'}`}>
          <Link to={`/view/${props.ep_meta.novel_id}/${order + 1}`}>次へ</Link>
        </div>
        <div className="index">
          <Link to={`/view/${props.ep_meta.novel_id}`}>目次へ</Link>
        </div>
        <div className={`previous${Number.isNaN(order) || order < 1 ? ' disabled' : ''}`}>
          <Link to={`/view/${props.ep_meta.novel_id}/${order - 1}`}>前へ</Link>
        </div>
      </div>
    </>
  );
};

const NextMenu: FC<Props> = (props: Props) => (
  <div className={`next-menu${props.visible ? ' shown' : ''}`}>
    {props.ep_meta ? <NextMenuBody ep_meta={props.ep_meta} nv_meta={props.nv_meta} /> : null}
  </div>
);

export default NextMenu;
